/* Overview.css */

.dashboard-container {
  display: flex;
  padding: 2rem;
  background-color: #f5f5f5;
}

.sidebar {
  width: 20%;
  background-color: #2d3748;
  color: white;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.sidebar h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.sidebar h1 {
  font-size: 2.5rem;
  color: #ffc107;
}

.sidebar-section {
  margin-top: 1.5rem;
}

.sidebar-item h4 {
  margin: 0;
  font-size: 1.2rem;
}

.positive {
  color: #2ed573;
}

.negative {
  color: #e74c3c;
}

.main-content {
  width: 75%;
  margin-left: 2rem;
}

.dashboard-row {
  display: flex;
  gap: 2rem;
  margin-bottom: 2rem;
}

.dashboard-card {
  flex: 1;
  background-color: white;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.large-card {
  flex: 2;
}

.dashboard-card h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1rem;
}

.amount {
  font-size: 1.8rem;
  font-weight: bold;
  margin-top: 1rem;
  color: #333;
}

.graph-placeholder {
  height: 150px;
  background-color: #e0e0e0;
  border-radius: 8px;
  margin: 1rem 0;
}

.multi-circle-container {
  position: relative;
  width: 100px;
  height: 100px;
  margin: 0 auto;
  margin-top: -20px;
}

.inner-circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  border: 5px solid;
  box-sizing: border-box;
}

.budget-amount {
  margin-top: 20px;
  font-size: 1.2rem;
  font-weight: bold;
  color: #333;
}