
/* Navbar.css */

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #2d3748; /* Dark blue background */
  color: #ffffff;
  padding: 1rem 3rem; /* Increased right padding to create a right shift */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  z-index: 100;
}

.navbar-brand {
  font-size: 1.4rem;
  font-weight: bold;
  margin-right: 2rem; /* Added margin for more space between brand and links */
}

.navbar-links {
  list-style: none;
  display: flex;
  gap: 2rem; /* Increased gap to add more space between links */
  margin: 0;
  padding: 0;
}

.navbar-links li {
  cursor: pointer;
  font-size: 1rem;
  color: #ffffff;
  transition: color 0.3s;
}

.navbar-links li:hover {
  color: #e2e8f0;
}  

.sign-out-container {
  margin-left: auto;
}

.sign-out-button {
  background-color: transparent;
  border: 1px solid #ffffff;
  color: #ffffff;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.sign-out-button:hover {
  background-color: #4a5568;
}
